@import url(https://fonts.googleapis.com/css2?family=Cedarville+Cursive&display=swap);
* {
    margin: 0;
}

.App {
    margin: 0;
    background-color: rgb(241, 241, 241);
    height: auto;
    width: auto;
    font-family: roboto;
}

.login {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background: url('http://qsfs.fs.quoracdn.net/-4-images.home.illo_1920.png-26-e9e6fbe02d908942.png');
}

.login__container {
    display: flex;
    flex-direction: column;
    padding: 20px;
    width: 700px;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
}

.login__logo {
    display: flex;
    justify-content: center;
}

.login__desc {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 10px;
}

.login__desc > p {
    color: gray;
    text-align: center;
    font-size: 20px;
    font-weight: 500;
}
.login__desc > h3 {
    margin-top: 10px;
    text-align: center;
    font-size: xx-large;
    font-family: 'Cedarville Cursive', cursive;
}

.login__auth {
    display: flex;
    margin-top: 50px;
}

.login__authOptions {
    display: flex;
    flex-direction: column;
    flex: 0.5 1;
    border-right: 1px solid lightgray;
    padding: 20px;
}

.login__authOption {
    display: flex;
    align-items: center;
    padding: 7px;
    border: 1px solid lightgray;
    border-radius: 5px;
    margin-bottom: 15px;
    cursor: pointer;
}

.login__authOption:hover {
    background-color: rgb(226, 226, 226);
}

.login__authOption > img {
    height: 30px;
    object-fit: contain;
}

.login__authOption > p {
    margin-left: 11px;
}

.login__authDesc {
    display: flex;
    align-items: center;
    padding: 7px;
    margin-bottom: 15px;
}

.login__authDesc > p {
    font-size: small;
    color: gray;
}

.login__emailPass {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 0.5 1;
    padding: 20px;
    margin-left: 20px;
}

.login__label > h4 {
    padding: 5px;
}

.login__inputFields {
    display: flex;
    flex-direction: column;
    padding: 5px;
}

.login__inputField {
    padding: 5px;
}

.login__inputField > input {
    padding: 10px;
    width: 100%;
    outline: none;
    border: none;
    background-color: rgb(236, 236, 236);
}

.login__forgButt {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 10px;
}

.login__forgButt > small {
    color: gray;
    cursor: pointer;
}

.login__forgButt > small:hover {
    color: blue;
    text-decoration: underline;
}

.login__forgButt > button {
    padding: 9px;
    border-radius: 5px;
    border: none;
    outline: none;
    background-color: blue;
    color: white;
    font-weight: bold;
    font-size: 15px;
    cursor: pointer;
}

.login__emailPass > button {
    padding: 9px;
    border-radius: 5px;
    border: none;
    outline: none;
    background-color: blue;
    color: white;
    font-weight: bold;
    font-size: 15px;
    cursor: pointer;
    margin-top: 10px;
}

.login__lang {
    display: flex;
    align-items: center;
    justify-content: center;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    margin-top: 30px;
    padding: 10px;
}

.login__lang > p {
    color: gray;
    margin-right: 5px;
    cursor: pointer;
    font-size: 13px;
}

.login__lang > p:hover {
    color: gray;
    margin-right: 5px;
    text-decoration: underline;
}

.login__lang > .MuiSvgIcon-root {
    color: gray;
}

.login__footer {
    padding-top: 10px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.login__footer > p {
    color: gray;
    font-size: small;
    cursor: pointer;
}

.login__footer > p:hover {
    text-decoration: underline;
}

.quora {
    background-color: #f4f4f4;
}

.quora__content {
    display: flex;
    justify-content: center;
    margin-top: 50px;
}

.feed {
    display: flex;
    flex-direction: column;
    flex: 0.6 1;
}

.post {
    display: flex;
    flex-direction: column;
    padding: 10px;
    border: lightgray solid 1px;
    background-color: white;
    border-radius: 5px;
    margin-bottom: 30px;
    box-shadow: 0px 0px 8px rgb(0, 0, 0, 0.3);
    font-family: roboto;
}

.post:hover {
    box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.4);
}

.post__info {
    display: flex;
    align-items: center;
    background-color: white;
    /* background-color: rgb(216, 214, 214); */
    color: black;
    /* color: lightgrey; */
    padding: 5px;
    border-radius: 3px;
}

.post__info > h4 {
    margin-left: 10px;
    cursor: pointer;
    font-weight: 700;
    font-size: 15px;
}

.post__info > h4:hover {
    text-decoration: underline;
}

.post__info > small {
    margin-left: 10px;
}

.post__body {
    display: flex;
    flex-direction: column;
}

.post__body > .post__question {
    margin-top: 10px;
    font-weight: bold;
    margin-bottom: 10px;
    cursor: pointer;
    display: flex;
    align-items: center;
    flex: 1 1;
}

.post__question > p {
    color: #14213d;
    font-weight: 700;
    font-size: 1.05em;
}

.post__footer {
    display: flex;
}
.post__answer {
    /* border-top: 1px black dashed; */
    margin-top: 10px;
}
.border__bottom {
    border-bottom: 3px #14213d solid;
    border-radius: 0 0 10px 10px;
}
.post__answer > .post__btnAnswer {
    margin-top: 10px;
    display: flex;
    margin-left: auto;
    cursor: pointer;
    padding: 7px;
    background-color: #14213d;
    outline: none;
    border: none;
    color: white;
    font-weight: 600;
    font-size: 18px;
    border-radius: 5px;
}

.post__btnAnswer:hover {
    color: #14213d;
    background: #fca311;
}

.post__question > p:hover {
    text-decoration: underline;
}

.post__body > .post__answer > p {
    margin-bottom: 10px;
}

.post__body > img {
    width: 100%;
    height: 400px;
    object-fit: contain;
    background-color: rgb(238, 238, 238);
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
}

.post__footer > .MuiSvgIcon-root {
    color: gray;
    margin-right: 40px;
    cursor: pointer;
}

.post__footerAction > button {
    background-color: lightgray;
    padding: 5px;
    align-items: center;
    justify-content: space-around;
    outline: none;
    border: none;
    background-color: #fca311;
    color: #14213d;
    cursor: pointer;
}

.post__footerAction > .upwardArrow {
    border-radius: 5px 0 0 5px;
    border-right: 1px #14213d solid;
}

.post__footerAction > .downwardArrow {
    border-radius: 0 5px 5px 0;
}

.post__footerAction > button > span {
    align-items: center;
    margin: 2px;
    padding: 2px;
}

.post__footerAction > .MuiSvgIcon-root {
    color: gray;
    margin-right: 40px;
    cursor: pointer;
}

.post__footerAction > .MuiSvgIcon-root:hover {
    color: rgb(0, 140, 255);
    margin-right: 40px;
}

.post__footerLeft {
    margin-left: auto;
}

.post__footerLeft > .MuiSvgIcon-root {
    color: gray;
    cursor: pointer;
    margin-left: 30px;
}

