/*-------------------------- for tags ----------------------------------*/

.tag-wrapper > p {
    /* border-bottom: 1px black dashed; */
    font-size: 1.2em;
    color: #49536b;
    font-weight: 700;
    background-color: rgb(241, 241, 241);
    /* border-radius: 5px; */
    border-bottom: 1px #6174a0 solid;
}

.tag-wrapper {
    /* background: white; */
    background: rgba(229, 229, 229, 0.45);
    width: 500px;
    margin: 0 auto;
    margin-top: 30px;
    padding: 20px 30px;
    text-align: center;
    border-radius: 5px;
    /* border-bottom: 1px #14213d solid; */
    /* box-shadow: 0px 3px 3px 0px rgba(50, 50, 50, 0.4); */
}
.tag {
    display: inline-block;
    position: relative;
    margin: 5px 10px 5px 0px;
}
.tag input[type='checkbox'] {
    opacity: 0;
    position: absolute;
    z-index: 300;
    cursor: pointer;
    left: 0;
    width: 100%;
    height: 100%;
}

.tag input[type='checkbox']:hover + label {
    background: #fca311;
}

.tag label {
    padding: 7px;
    position: relative;
    z-index: 200;
    color: #14213d;
    display: block;
    border-radius: 16px;
    background: white;
    transition: all 0.08s ease-in;
}

.tag .fa {
    position: absolute;
    z-index: 250;
    right: 11px;
    top: 9px;
    color: white;
    line-height: 0.95;
}
.tag .fa-check {
    display: none;
    top: 8px;
    right: 10px;
    transition: all 0.08s ease-in;
}
.tag input[type='checkbox']:checked + label {
    background: #354a77;
    color: white;
    animation: flick 0.08s;
}
.tag input[type='checkbox']:checked ~ .fa-plus {
    display: none;
}
.tag input[type='checkbox']:checked ~ .fa-check {
    animation: flick 0.08s;
    display: block;
}
@keyframes flick {
    60% {
        transform: scale(1.15);
    }
    100% {
        transform: scale(1);
    }
}

